.nav-button {
    border: 1px solid rgb(205, 221, 221);
    background-color: rgb(45, 78, 51);
    color: rgb(205, 221, 221);
    text-align: center;
    padding: 5px;
    display: grid;
    cursor: pointer;
    text-decoration: none;
}

.nav-button:hover {
    border: 1px solid rgb(45, 78, 51);
    background-color: rgb(205, 221, 221);
    color: rgb(45, 78, 51);
}

@media screen and (prefers-color-scheme: light) {
    .nav-button {
        border: 1px solid rgb(46, 47, 47);
        background-color: rgb(205, 221, 221);
        color: rgb(46, 47, 47);
    }

    .nav-button:hover {
        border: 1px solid rgb(205, 221, 221);
        background-color: rgb(46, 47, 47);
        color: rgb(205, 221, 221);
    }
}