* {
    box-sizing: border-box;
}

[class*="col-"] {
    width: 100%;
    float: left;
    padding: 15px;
}

@media only screen and (max-width: 768px) {
    .non-mobile-only {
        visibility: hidden;
        display: none;
    }
}

@media only screen and (min-width: 600px) and (max-width: 768px) {
    .font-8-s {
        font-size: 8pt;
    }

    .font-7-s {
        font-size: 7pt;
    }
}

@media only screen and (max-width: 600px) {
    .non-es-only {
        visibility: hidden;
        display: none;
    }
}

@media only screen and (min-width: 600px) {
    .col-s-1 {width: 12.5%;}
    .col-s-2 {width: 25%;}
    .col-s-3 {width: 37.5%;}
    .col-s-4 {width: 50%;}
    .col-s-5 {width: 62.5%;}
    .col-s-6 {width: 75%;}
    .col-s-7 {width: 87.5%;}
    .col-s-8 {width: 100%;}

    .mobile-only {
        visibility: hidden;
        display: none;
    }

    .non-es-right-align {
        text-align: right;
    }
}

@media only screen and (min-width: 768px) {
    .col-m-1 {width: 8.33%;}
    .col-m-2 {width: 16.67%;}
    .col-m-3 {width: 25%;}
    .col-m-4 {width: 33.33%;}
    .col-m-5 {width: 41.67%;}
    .col-m-6 {width: 50%;}
    .col-m-7 {width: 58.33%;}
    .col-m-8 {width: 66.67%;}
    .col-m-9 {width: 75%;}
    .col-m-10 {width: 83.33%;}
    .col-m-11 {width: 91.67%;}
    .col-m-12 {width: 100%;}
}

@media only screen and (min-width: 992px) {
    .col-l-1 {width: 6.25%;}
    .col-l-2 {width: 12.5%;}
    .col-l-3 {width: 18.75%;}
    .col-l-4 {width: 25%;}
    .col-l-5 {width: 31.25%;}
    .col-l-6 {width: 37.5%;}
    .col-l-7 {width: 43.75%;}
    .col-l-8 {width: 50%;}
    .col-l-9 {width: 56.25%;}
    .col-l-10 {width: 62.5%;}
    .col-l-11 {width: 68.75%;}
    .col-l-12 {width: 75%;}
    .col-l-13 {width: 81.25%;}
    .col-l-14 {width: 87.5%;}
    .col-l-15 {width: 93.75%;}
    .col-l-16 {width: 100%;}
}

@media only screen and (max-width: 1200px) {
    .full-only {
        visibility: hidden;
        display: none;
    }
}

@media only screen and (min-width: 1200px) {
    .col-1 {width: 5%;}
    .col-2 {width: 10%;}
    .col-3 {width: 15%;}
    .col-4 {width: 20%;}
    .col-5 {width: 25%;}
    .col-6 {width: 30%;}
    .col-7 {width: 35%;}
    .col-8 {width: 40%;}
    .col-9 {width: 45%;}
    .col-10 {width: 50%;}
    .col-11 {width: 55%;}
    .col-12 {width: 60%;}
    .col-13 {width: 65%;}
    .col-14 {width: 70%;}
    .col-15 {width: 75%;}
    .col-16 {width: 80%;}
    .col-17 {width: 85%;}
    .col-18 {width: 90%;}
    .col-19 {width: 95%;}
    .col-20 {width: 100%;}
}

.row::after {
    content: "";
    clear: both;
    display: table;
}