.header {
    background-color: rgb(45, 78, 51);
    text-align: center;
    padding-top: 50px;
    padding-bottom: 50px;
    color: rgb(136, 187, 146);
    cursor: pointer;
    display: block;
    font-size: 1.5em;
    margin-top: 0.83em;
    margin-bottom: 0.83em;
    margin-left: 0;
    margin-right: 0;
    font-weight: bold;
    text-decoration: none;
}

@media screen and (prefers-color-scheme: light) {
    .header {
        background-color: rgb(136, 187, 146);
        color: rgb(45, 78, 51);
    }
}